/*    
:   ,       
*/
import React, { useEffect, useState, useContext } from 'react'
import aronlogo from '../image/aronlogo.jpg'
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import UserContext from '../contexts/UserContext';

// useState  (id), (pw), 
//     (idValid, pwValid)  . 
//     (notAllow)  .
const Login = () => {

    const [id, setId] = useState("");
    const [pw, setPw] = useState("");
    const { setUser } = useContext(UserContext);

    //   state
    const [idValid, setIdValid] = useState(false);
    const [pwValid, setPwValid] = useState(false);

    //    (  )
    const [notAllow, setNotAllow] = useState(true);
    const navigate = useNavigate();

    // 'touched' state 
    const [idTouched, setIdTouched] = useState(false);
    const [pwTouched, setPwTouched] = useState(false);

    // handleId handlePw  
    //       . 
    //    8 , 
    //  8  , ,     .
    const handleId = (e) => {
        // onchange   handleId  e.target.value 
        const newId = e.target.value;
        setId(newId);
        setIdTouched(true);  //   'touched' 
        //  8     ()
        const idRegex = /^\d{8}$/;
        //    true  false 
        if (idRegex.test(newId)) {
            setIdValid(true);
        } else {
            setIdValid(false);
        }
    }

    const handlePw = (e) => {
        const newPw = e.target.value;
        setPw(newPw);
        setPwTouched(true);  //   'touched' 
        //, ,   8      ()
        const pwRegex = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/;
        if (pwRegex.test(newPw)) {
            setPwValid(true);
        } else {
            setPwValid(false);
        }
    }

    //   User id, pw id pw  
    // ->  /   ->   
    // onClickconfirmButton      
    //       . 
    //     context localStorage , 
    //   .
    const onClickconfirmButton = async () => {
        try {
            const response = await axios.post('http://localhost:3001/user/login', { id, pw });
            if (response.data.msg === 'success') {
                const user = {
                    job: response.data.user.class,
                    name: response.data.user.name
                };
                setUser(user); //    UserContext setUser    
                localStorage.setItem('user', JSON.stringify(user)); //     
                localStorage.setItem('loginTime', Math.floor(Date.now() / 1000)); //     
                alert(' ');
                navigate('/main1')
            } else {
                alert('  ');
            }
        } catch (error) {
            console.error(error);
        }
    };

    // useEffect        
    //      .
    useEffect(() => {
        const user = localStorage.getItem('user');
        if (user) {
            //    API     '/main1' 
            axios.get('/check-login')
                .then(response => {
                    if (response.data.loggedIn) {
                        navigate('/main1');
                    }
                })
                .catch(error => {
                    console.error('   ', error);
                });
        }
    }, []);


    // idValid pwValid     
    //   setNotAllow(true) ,     
    useEffect(() => {
        if (idValid && pwValid) {
            setNotAllow(false);
            return;
        } setNotAllow(true);
    }, [idValid, pwValid]);

    return (
        <div className='page'>

            <div className="logo-wrap">
                <img className="logo-img" src={aronlogo} alt="" />
            </div>

            <div className="content-wrap">
                <div className="input-wrap">
                    <input type="text" className='input' placeholder='' value={id} onChange={handleId} />
                </div>
                <div className="error-message-wrap">
                    {!idValid && id.length > 0 && (
                        <div> </div>
                    )}
                </div>
                <div className="input-wrap">
                    <input type="password" className='input' placeholder='' value={pw} onChange={handlePw} />
                </div>
                <div className="error-message-wrap">
                    {!pwValid && pw.length > 0 && (
                        <div>,,  8  </div>
                    )}
                </div>
                <div>
                    {/* disabled  :      */}
                    <button onClick={onClickconfirmButton} className='bottom-button' disabled={notAllow}>LOGIN</button>
                </div>
            </div>


        </div>
    )
}

export default Login