import React from 'react'
import { ResponsiveBar } from '@nivo/bar'
import { useState } from 'react'
import './Graph.css'

const GraphBar = () => {

    //   HR, SBP, O2Sat, MAP, DBP    
    const DBdata = [
        {
            "Time": "00:00",
            'HR': 110,
            "SBP": 120,
            "O2Sat": 98,
            "MAP": 113,
            "DBP": 112,
        },
        {
            "Time": "04:00",
            "HR": 111,
            "SBP": 119,
            "O2Sat": 99,
            "MAP": 108,
            "DBP": 111,
        },
        {
            "Time": "08:00",
            "HR": 107,
            "SBP": 119,
            "O2Sat": 99,
            "MAP": 108,
            "DBP": 111,
        },
        {
            "Time": "12:00",
            "HR": 104,
            "SBP": 120,
            "O2Sat": 97,
            "MAP": 116,
            "DBP": 113,
        },
        {
            "Time": "16:00",
            "HR": 106,
            "SBP": 120,
            "O2Sat": 98,
            "MAP": 110,
            "DBP": 112,
        },
        {
            "Time": "20:00",
            "HR": 112,
            "SBP": 121,
            "O2Sat": 96,
            "MAP": 118,
            "DBP": 117,
        }
      ]

    // 'keys'  ,  'DBdata'      'Time'    
    const [keys, setKeys] = useState(Object.keys(DBdata[0]).filter(key => key !== 'Time'));

    // 'key'   'keys' 'key'   'key'   ,
    //    'key'    'keys'  

    // key -->      
    // item --> keys   
    // ,  'HR'   'HR' 'key' handleCheckboxChange  ,
    //   'keys'   'HR'    

    const handleCheckboxChange = (key) => {
        if (keys.includes(key)) {
        setKeys(keys.filter(item => item !== key));
        } else {
        setKeys([...keys, key]);
        }
    };

    // MyResponsiveBar 
    // 'data', 'keys' props  '@nivo/bar' 'ResponsiveBar'  
    //   props  , ,   
    const MyResponsiveBar = ({ data, keys }) => {

        return (
            <ResponsiveBar
                data={data}
                keys={keys}
                indexBy="Time"
                margin={{ top: 50, right: 130, bottom: 50, left: 60 }}
                groupMode="grouped"
                valueScale={{ type: 'linear' }}
                indexScale={{ type: 'band', round: true }}
                colors={{ scheme: 'nivo' }}
                defs={[
                    {
                        id: 'dots',
                        type: 'patternDots',
                        background: 'inherit',
                        color: '#38bcb2',
                        size: 4,
                        padding: 1,
                        stagger: true
                    },
                    {
                        id: 'lines',
                        type: 'patternLines',
                        background: 'inherit',
                        color: '#eed312',
                        rotation: -45,
                        lineWidth: 6,
                        spacing: 10
                    }
                ]}        
                axisTop={null}
                axisRight={null}
                axisBottom={{
                    tickSize: 5,
                    tickPadding: 5,
                    tickRotation: 0,
                    legend: 'Time',
                    legendPosition: 'middle',
                    legendOffset: 32
                }}
                axisLeft={{
                    tickSize: 5,
                    tickPadding: 5,
                    tickRotation: 0,
                    legend: 'score',
                    legendPosition: 'middle',
                    legendOffset: -40
                }}
                labelSkipWidth={12}
                labelSkipHeight={12}
                labelTextColor={{
                    from: 'color',
                    modifiers: [
                        [
                            'darker',
                            1.6
                        ]
                    ]
                }}
                legends={[
                    {
                        dataFrom: 'keys',
                        anchor: 'bottom-right',
                        direction: 'column',
                        justify: false,
                        translateX: 120,
                        translateY: 0,
                        itemsSpacing: 2,
                        itemWidth: 100,
                        itemHeight: 20,
                        itemDirection: 'left-to-right',
                        itemOpacity: 0.85,
                        symbolSize: 20,
                        effects: [
                            {
                                on: 'hover',
                                style: {
                                    itemOpacity: 1
                                }
                            }
                        ]
                    }
                ]}
                role="application"
                ariaLabel="Nivo bar chart demo"
                barAriaLabel={e=>e.id+": "+e.formattedValue+" in country: "+e.indexValue}
            />
        )
    };
    console.log(DBdata)
  return (
    // 'keys'     ,   'handleCheckboxChange' 
    // 'keys' -->      
    //  'MyResponsiveBar'   , 'DBdata' 'keys' props 
    <div className='graph-size'>
      <div className='checkbox-wrap'>
        <div className="index-name">
            <input type="checkbox" checked={keys.includes('HR')} onChange={() => handleCheckboxChange('HR')} /> HR
        </div>
        <div className="index-name">
            <input type="checkbox" checked={keys.includes('O2Sat')} onChange={() => handleCheckboxChange('O2Sat')} /> O2Sat
        </div>
        <div className="index-name">
            <input type="checkbox" checked={keys.includes('SBP')} onChange={() => handleCheckboxChange('SBP')} /> SBP
        </div>
        <div className="index-name">
            <input type="checkbox" checked={keys.includes('MAP')} onChange={() => handleCheckboxChange('MAP')} /> MAP
        </div>
        <div className="index-name">
            <input type="checkbox" checked={keys.includes('DBP')} onChange={() => handleCheckboxChange('DBP')} /> DBP
        </div>
      </div>
        <MyResponsiveBar data={DBdata} keys={keys}/>
    </div>
  )
}

export default GraphBar;