/*    
:    ,   
*/
import React, { useState, useEffect, useContext } from "react";
import { Routes, Route, Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import "../components/Header.css";
import aronWhite from "../image/aronWhite.png";
import iconLogout from "../image/iconLogout.svg"
import UserContext from '../contexts/UserContext';  // UserContext import

const Header = () => {
  const { user, setUser } = useContext(UserContext);

  //       ( 60)
  const [time, setTime] = useState(60 * 60);
  const navigate = useNavigate();

  // 1    
  useEffect(() => {
    const timerId = setInterval(() => {
      setTime(prevTime => prevTime - 1);
    }, 1000);

    //     
    return () => {
      clearInterval(timerId) 
    }
  }, []);

  //    mm:ss  
  const formatTime = (time) => {
    const minutes = Math.floor(time / 60);
    const seconds = time % 60;
    // minutes 10   0  '01', '02'  , 10   
    // seconds 
    return `${minutes < 10 ? `0${minutes}` : minutes}:${seconds < 10 ? `0${seconds}` : seconds}`;
  }

 //    localStorage 
 //      UserContext time  
  useEffect(() => {
    const savedUser = localStorage.getItem('user');
    const loginTime = localStorage.getItem('loginTime');
    if (savedUser && loginTime) {
      const currentTime = Math.floor(Date.now() / 1000); //   ( )
      const elapsedTime = currentTime - loginTime; //     ( )
      const remainingTime = 60 * 60 - elapsedTime; //   ( )
      setUser(JSON.parse(savedUser));
      setTime(remainingTime > 0 ? remainingTime : 0); //   0  0 
    }
  }, []);

  //     
  const logout = async () => {
    try {
      const response = await axios.post('http://localhost:3001/user/logout', {}, { withCredentials: true });
      if (response.data.msg === 'success') {
        alert(' ');
        localStorage.removeItem('user');
        document.cookie = 'user=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;';
        navigate('/login');
        //     
      window.history.pushState(null, document.title, window.location.href);
      } else {
        throw new Error('  ');
      }
    } catch (error) {
      console.error(' :', error);
      alert(' : ' + error.message);
    }
  };

  //      
  useEffect(() => {
    if (time === 0) {
      // time  0     
      alert(' ');
      navigate('/login')
    }
  }, [time]);

  ///      
  const onClickLogout = () => {
    if (!user) {
      alert(' .');
      return;
    }
    logout();
  }

  return (
    <div className="header-page">
      <div className="header-content">
        <Link to = '/main1'>
          <img src={aronWhite} alt="" className="header-logo" />
        </Link>
        <ul>
          <li>
          {user ? <span>{user.job === 0 ? '' : ''} [{user.name}]</span> : <span>  </span>}
        </li>
        <li>
          <span className="login-time">{formatTime(time)}</span>
        </li>
        <li>
          <div className="logout-wrapper" onClick={onClickLogout}>
            <span className="logout-content"></span>
            <button className="icon-logout-wrap"><img src={iconLogout} className="icon-logout" alt=" " /></button>
          </div>
        </li>
      </ul>
    </div>
  </div>
);
};

export default Header;